import React, { useReducer } from 'react'
import NumericPad from './NumericPad'

export const ACTIONS = {
    ADD_DIGIT: 'add-digit',
    REMOVE_DIGIT: 'remove-digit',
    CLEAR: 'clear'
}

function handleDispatch(number, action) {
    switch(action.type) {
        case ACTIONS.ADD_DIGIT:
            return action.value + number * 10 ;
        case ACTIONS.REMOVE_DIGIT:
            return Math.floor(number / 10);
        case ACTIONS.CLEAR:
            return 0;
        default:
            return number;
    }
}

function App() {
    const [value, dispatch] = useReducer(handleDispatch, 0);

    return (
        <div>
            <div>
                <input disabled value={Math.round(value*1.9)} />
            </div>
            <div>
                <input disabled value={Math.ceil(value*0.5)} />    
            </div>        
            <NumericPad dispatch={dispatch} value={value} />
        </div>
    )
}

export default App
