import React from 'react'
import {ACTIONS} from './App.js'

export default function NumericPad({dispatch, value}) {
    return (
        <div>
            <input disabled value={value}/>
            <div>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 1})}>1</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 2})}>2</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 3})}>3</button>
                <button onClick={() => dispatch({ type: ACTIONS.REMOVE_DIGIT})}>CE</button>
            </div>
            <div>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 4})}>4</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 5})}>5</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 6})}>6</button>
                <button onClick={() => dispatch({ type: ACTIONS.CLEAR})}>C</button>
            </div>
            <div>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 7})}>7</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 8})}>8</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 9})}>9</button>
                <button onClick={() => dispatch({ type: ACTIONS.ADD_DIGIT, value: 0})}>0</button>
            </div>        
        </div>
    )
}
